
import "@/components/common/table.css";
import { GenericTreeNode } from "@/features/trees/tree";
import { processedPropType } from "@/util/vue";
import { defineComponent, unref } from "vue";
import TreeNode from "./TreeNode.vue";

export default defineComponent({
    props: {
        nodes: {
            type: processedPropType<GenericTreeNode[][]>(Array),
            required: true
        },
        leftSideNodes: processedPropType<GenericTreeNode[]>(Array),
        rightSideNodes: processedPropType<GenericTreeNode[]>(Array)
    },
    components: { TreeNode },
    setup() {
        function gatherNodeProps(node: GenericTreeNode) {
            const {
                display,
                visibility,
                style,
                classes,
                tooltip,
                onClick,
                onHold,
                color,
                glowColor,
                forceTooltip,
                canClick,
                mark,
                id
            } = node;
            return {
                display,
                visibility,
                style,
                classes,
                tooltip,
                onClick,
                onHold,
                color,
                glowColor,
                forceTooltip,
                canClick,
                mark,
                id
            };
        }

        return {
            gatherNodeProps,
            unref
        };
    }
});
